*,
*:before,
*:after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}


body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    text-align: center;
    font-weight: 400;
    color: #fff;
    background: #4B3869;
    font-size: 27px;
    padding: 5px 0;
}

.inputHolder {
    margin: 15px 0;
}

.mainWrapper {
    padding: 0 10px;
}

.mainContent {
    display: flex;
    align-items: center;
    margin-bottom: 10px;

    @media (max-width: 800px) {
        flex-direction: column;
    }
}

.inputHolder { 
    padding: 0 10px;
    display: flex; 

    input[type='text'] {
        max-width: 400px;
        width: 100%;
        padding: 5px 10px;
        font-size: 16px;
        border-radius: 0;
        display: inline-block;
        border: 1px solid #222;
        outline: none;
    }

    .uploadInput { 
        width: 0px;
        height: 0px;
        visibility: hidden;
        opacity: 0;
    }

    .or {
        align-items: center;
        display: inline-flex;
        padding: 0 20px;
    }

    .uploadImage {
        padding: 8px 16px;
        font-size: 16px;
        background: #664E88;
        color: #fff;
        display: inline-block;
        border: 0;
        
        cursor: pointer;
        outline: none;
        border-radius:5px;
    }
}

.imageHolder {
    width: 60%;
    display: block;
    flex: 0 0 60%;
    max-width: 900px;
    height: 75vmin;
    

    @media (max-width: 800px) {
        width: 100%;
        height: auto;
        margin-bottom: 15px;
    }

    &:empty {
        display: none;
    }

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius:2px;
    }
}

.resultsHolder {
    padding: 25px;


    @media (max-width: 800px) {
        width: 90%;
        padding: 0;
    }

    .result {
        border: 1px solid #333;
        border-radius:3px;
        margin: 0 0 12px;
        padding: 15px;

        &:first-of-type {
            border-color: #333;
            background-color: #565b6b;

            .name { color: #fff; }
            .confidence { color: #dfdfdf; }
        }

        .name {
            font-size: 18px;
            font-weight: 600;
            text-transform: uppercase;
        }

        .confidence {
            display: block;
            font-weight: 300;

            .bestGuess {
                background: #fff;
                padding: 2px 6px;
                color: #333;
                margin-left: 5px;
                border-radius:5px;
            }
        }
    }
}

.button {
    margin-top:5px;
    padding: 10px 20px;
    font-size: 18px;
    background: #664E88;
    color: #fff;
    display: inline-block;
    border: 0;
    border-radius: 5px;
    cursor: pointer;
    outline: none;
    
}


.recentPredictions {
    margin: 20px 10px 0;
    background: #565b6b;
    padding: 20px;
    border-top-left-radius:3px;
    border-top-right-radius:3px;

    h2 {
        color: rgb(255, 255, 255);
        font-size: 24px;
        margin: 10px 0;
        
    }
    
    .recentImages {
        display: flex;
        flex-wrap: wrap;
        
    }
    img {
        padding:5px;
        border-radius:7px;
    }



    .recentPrediction {
        flex: 0 0 20%;
        cursor: pointer;
        

        @media (max-width: 1100px) {
            flex: 0 0 25%;
            
        }

        @media (max-width: 870px) {
            flex: 0 0 33.3%;
            
        }

        @media (max-width: 700px) {
            flex: 0 0 50%;
            
            
        }

        img {
            display: block;
            width: 100%;
            height: 200px;
            object-fit: cover;

            @media (max-width: 700px) {
                height: 190px;
            }
        }
       
    }
}

.name-footer {
    text-align: center;
    font-weight:100;
    font-size: 12px;
    padding: 3px 0;
    border-top-left-radius:2px;
    border-top-right-radius:2px;

    background:#f0f0f0;
    
    bottom:0;
    width:100%;
    color:#808080;
}
